.row{
    height: 200px;
}
.posters{ 
    padding: 35px;
    margin-top: 160px;
    color: #fff;
    overflow-x: scroll;
    overflow-y: hidden;
   
   
}
.posters::-webkit-scrollbar{
    display: none;
}
.small-poster-image::-webkit-scrollbar{
    /*display: none;*/
}
.poster-image{
    height: 180px;
    padding-left: 1px;
    margin-right: 10px;
    cursor: pointer;
    
    
}

.poster-image-main{
    display: flex;
    margin-top: 15px;
    
}
.small-poster-image{
    height: 150px;
    padding-left: 5px;
    margin-right: 10px;
    cursor: pointer;
    margin-top: -13px;
    
    
}
.small-poster-image:hover{
    transform: scale(1.2,1.2);
}
.poster-image:hover{
    transform : scale(1.2,1.2);
}
.title{
    margin-top: 41px;
   
}
